body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  font-size: 16px;
  line-height: 1.5;
  height: 100%;

  padding: 0;
  margin: 0; }
* {
  box-sizing: border-box; }

.container {
  max-width: 1190px;
  width: 100%;
  margin: 0 auto; }

@import "components/index";
@media screen and (max-width: 576px) {
  .container {
    padding-left: 25px;
    padding-right: 25px; } }

