.flex {
  display: flex;
  &_jcsb {
    justify-content: space-between; }
  &_aic {
    align-items: center; }
  &_fww {
    flex-wrap: wrap; }
  &_ais {
    align-items: stretch; }
  &_jcfs {
    justify-content: flex-start; } }

.first {
  &__image {
    flex-basis: 45%;
    img {
      width: 100%;
      vertical-align: top; } }
  &__right {
    flex-basis: 45%;
    h1 {
      font-size: 42px;
      font-weight: bold; } } }
h2 {
  text-align: center;
  font-size: 32px; }
.about {
  &__right {
    flex-basis: 45%;
    img {
      width: 100%;
      vertical-align: top; } }
  &__left {
    flex-basis: 45%; } }
@media screen and (max-width: 768px) {
  .section_first {
    .flex {
      flex-direction: column; } }
  .first__right {
    padding: 0 20px;
    h1 {
      font-size: 38px; } }
  .about {
    .flex {
      flex-direction: column; } } }

